import Vue from 'vue';
import { BASE_URL, PROTOCOL, CONTAIHER_TOP } from '../config/index.js';
function waitSendVerificationCode(frequency,callbrak) {
    if(frequency > 0) {
        callbrak && callbrak();
        frequency --;
        setTimeout(waitSendVerificationCode,1000,frequency,callbrak);
    } else {
        callbrak && callbrak();
    }
}
document.getElementById("registered").style.top = CONTAIHER_TOP;
new Vue({
    el: '#registered',
    data: {
        email: "",
        password: "",
        password_confirmation: "",
        token: "",
        salesman: "0",
        agreeChecked: true,
        get_token: {
            verification_code: "",
            captcha_src: "",
            wait_time: 0,
            btn_text: ""
        },
        errors: {
            email: false,
            password_confirmation: false
        },
        salesmans: [
            
        ]
    },
    methods: {
        agree() {
            this.agreeChecked = !this.agreeChecked;
            console.log(this.agreeChecked);
        },
        registered() {
            if(this.agreeChecked) {
                $.post(PROTOCOL+"//"+BASE_URL+"/auth/registerByEmail",{
                    email: this.email,
                    password: this.password,
                    password_confirmation: this.password_confirmation,
                    token: this.token,
                    salesman: this.salesman,
                },(data) => {
                    if(data.code==1) {
                        alert("");
                        this.email="";
                        this.password="";
                        this.password_confirmation="";
                        this.token = "";
                        this.get_token.verification_code = "";
                        location.href = "http://"+BASE_URL+"/tz";
                    } else {
                        alert(data.msg);
                        this.switchCaptchaSrc();
                    }
                });
            } else {
                alert("");
            }
        },
        getToken() {
            if(this.get_token.wait_time > 0) {
                return ;
            }
            
            $.post(PROTOCOL+"//"+BASE_URL+"/auth/sendEmailCode",{
                email: this.email,
                captcha: this.get_token.verification_code
            },(data) => {
                if(data.code == 1) {
                    alert("");
                    this.get_token.wait_time = 60;
                    this.get_token.btn_text = `${this.get_token.wait_time}`;
                    waitSendVerificationCode(this.get_token.wait_time,() => {
                        if(this.get_token.wait_time > 0) {
                            this.get_token.wait_time --;
                            this.get_token.btn_text = `${this.get_token.wait_time}`;
                        } else {
                            this.get_token.btn_text = "";
                        }
                    });
                } else {
                    alert(data.msg);
                    this.switchCaptchaSrc();
                }
            });
        },
        switchCaptchaSrc() {
            $.get(PROTOCOL+"//"+BASE_URL+"/verification_code",(data) => {
                if(data.code==1) {
                    this.get_token.captcha_src = data.data.src;
                }
            });
        }
    },
    mounted() {
        $.post(PROTOCOL+"//"+BASE_URL+"/auth/sendEmailCode",{
            email: "515149416@qq.com",
            captcha: "1234"
        },() => {
            $.get(PROTOCOL+"//"+BASE_URL+"/verification_code",(data) => {
                if(data.code==1) {
                    // this.get_token.captcha_src = data.data.src;
                    this.switchCaptchaSrc();
                    this.salesmans = [];
                    $.get(PROTOCOL+"//"+BASE_URL+"/auth/getAllSalesman", (res) => {
                        if(res.code==1) {
                            this.salesmans = res.data;
                        }
                    });
                }
            });
        });
        
    },
    watch: {
        email(newEmail, oldEmail) {
            if(!/^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/.test(newEmail)) {
                this.errors.email = true;
            } else {
                this.errors.email = false;
            }
        },
        password_confirmation(newPassword, oldPassword) {
            if(newPassword!=this.password) {
                this.errors.password_confirmation = true;
            } else {
                this.errors.password_confirmation = false;
            }
        }
    }
});